@import "../../styles/variables";
@import "../../styles/mixins";

.img {
    width: 100%;
    margin-top: -5rem;
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0% 100%);
    z-index: -1;
    object-fit: cover;

    @include respond($tab-land) {
        height: 50vh;
    }
}

.nav {
    z-index: 100;
    width: 100%;
    height: 8rem;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: $color-white;
    background-color: $color-secondary;
    box-shadow: 6px 5px 19px -5px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 6px 5px 19px -5px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 6px 5px 19px -5px rgba(0, 0, 0, 0.75);

    &__caption {
        margin-left: 7%;
    }
}
